import React from 'react'
import PropTypes from 'prop-types'

import Row from './Row'
import id from './keyKeeper'

const RowContainer = (props) => {

  const onAppendLesson = () => {
    if(props.tableLocked) return;
    let day ={};
    Object.assign(day, props.day);

    if(day.lessons){
      day.lessons.push({ title: '', type: 3, sId: day.lessons.length });
    }else {
      day.lessons = [{title: '', type: 3, sId: 0}];
    }
    props.onContentChange(day);
  }

  const onPopLesson = () => {
    if(props.tableLocked) return;

    let day ={};
    Object.assign(day, props.day);

    if(day.lessons){
      day.lessons.pop();
    }else {
      return;
    }
    props.onContentChange(day);
  }

  const onPinSubject = (cellSerial) => {
    if(props.tableLocked) return;

    let dayCopy = {};
    Object.assign(dayCopy, props.day);
    for (let index in dayCopy.lessons){
      if (index == cellSerial){
        dayCopy.lessons[index] = {
          ...dayCopy.lessons[index],
          title: props.selectedSubject.name,
          type: 3
        }
        return props.onContentChange(dayCopy);
      }
    }
  }

  const onClearSubject = (cellSerial) => {
    if(props.tableLocked) return;

    let dayCopy = {};
    Object.assign(dayCopy, props.day);

    for (let index in dayCopy.lessons){
      if (index == cellSerial){
        dayCopy.lessons[index] = {
          ...dayCopy.lessons[index],
          title: '',
          type: 3
        }
        return props.onContentChange(dayCopy);
      }
    }
  }

  const onToggleLessonType = (cellSerial) => {
    let dayCopy = {};
    Object.assign(dayCopy, props.day);

    for (let index in dayCopy.lessons){
      if (index == cellSerial){
        let states = [3, 2, 1, 1];
        dayCopy.lessons[index] = {
          ...dayCopy.lessons[index],
          type: states[dayCopy.lessons[index].type]
        }
        return props.onContentChange(dayCopy);
      }
    }
  }


  return <Row
    onToggleLessonType={onToggleLessonType}
    onClearSubject={onClearSubject}
    tableLocked={props.tableLocked}
    selectedSubject={props.selectedSubject}
    onPinSubject={onPinSubject}
    onAppendLesson={onAppendLesson}
    onPopLesson={onPopLesson}
    key={id()}
    day={props.day} />
}

RowContainer.propTypes = {

}

export default RowContainer;



