import React, {Component} from 'react'
import Cell from './Cell'

const lessonTypesEnum = [
  '',
  'Pednka',
  'Cvien',
  'Neureno'
]

class CellConteiner extends Component {
  constructor(props){
    super(props);
    this.state = {
      displayControls: false
    }
    this.onHovered = this.onHovered.bind(this);
  }

  onHovered(){
    if(this.props.tableLocked) return;
    this.setState({displayControls: !this.state.displayControls});
  }

  processLesson(lesson){
    let lessonCopy = {};
    Object.assign(lessonCopy, lesson);

    lessonCopy.type = lessonTypesEnum[lessonCopy.type];
    return lessonCopy;
  }

  render(){
    let lesson = this.processLesson(this.props.lesson);

    return <Cell
      onToggleLessonType={this.props.onToggleLessonType}
      selectedSubject={this.props.selectedSubject}
      onHovered={this.onHovered}
      showControls={this.state.displayControls}
      onPinSubject={this.props.onPinSubject}
      onClearSubject={this.props.onClearSubject}
      serial={this.props.serial}
      lesson={lesson}
      displayControls={this.state.displayControls}/>
  }
}

export default CellConteiner;


