import React, {Component} from 'react'
import PropTypes from 'prop-types'

import Table from './Table'
import id from './keyKeeper'

const dayEnum = ["Pondl", "ter", "Steda", "tvrtek", "Ptek"];

class TableContainer extends Component
{
  constructor(props){
    super(props);
    this.state = {
      tableLocked: true
    }
    this.onContentChange = this.onContentChange.bind(this);
    this.onToggleTableLock = this.onToggleTableLock.bind(this);
  }

  onToggleTableLock(){
    this.setState({tableLocked: !this.state.tableLocked})
  }

  onContentChange(data){
    let newObj = [...this.props.days];
    let found = false;
    for (let it of newObj) {
      if (it.dId === data.dId) {
        it = data;
        found = true;
        break;
      }
    }
    if (!found) {
      newObj.push(data);
    }
    return this.props.onContentChange(newObj);
  }

  render()
  {
    let longest = 0;
    let newDaysObj = [];
    for (let i = 0; i < 5; i++) {
      if (this.props.days[i]) {
        newDaysObj[this.props.days[i].dayNo] = {
          ...this.props.days[i],
          dayName: dayEnum[this.props.days[i].dayNo]
        };
      }

      if (!newDaysObj[i]) {
        newDaysObj[i] = {
          dId: id(),
          dayNo: i,
          dayName: dayEnum[i]
        }
      }

      if (newDaysObj[i].lessons && newDaysObj[i].lessons.length > longest)
        longest = newDaysObj[i].lessons.length;
    }

    const toTimeString = (fromMdnt) => {
      let min = fromMdnt % 60;
      let hour = (fromMdnt - min) / 60;

      min = min < 10 ? '0' + min : min;
      hour = hour < 10 ? '0' + hour : hour;

      min = hour >= 24 ? '' : min;
      hour = hour >= 24 ? '' : hour;

      return {
        min,
        hour
      }
    }

    let time = [];
    let zero = (this.props.termines.startTime.hours * 60 + this.props.termines.startTime.mins);
    const lessonAndPause = this.props.termines.lessonDuration + this.props.termines.pauseDuration;
    zero -= lessonAndPause;

    for (let i = 0; i < longest; i++) {
      let pom = zero + i * (lessonAndPause);
      let end = pom + this.props.termines.lessonDuration;
      time.push({
        start: toTimeString(pom),
        end: toTimeString(end)
      });
    }

    return <Table
      selectedSubject={this.props.selectedSubject}
      tableLocked={this.state.tableLocked}
      onToggleTableLock={this.onToggleTableLock}
      onWeekTypeChange={this.props.onWeekTypeChange}
      onContentChange={this.onContentChange}
      weekType={this.props.weekType}
      times={time}
      days={newDaysObj}/>
  }
}

TableContainer.propTypes = {

}

export default TableContainer;


