import React from 'react'
import Radium from 'radium'
import id from './keyKeeper'

const styles = {
  layout: {
    color: '#3a3a3a',
    display: 'flex',
    flex: 1,
    flexFlow: 'column wrap',
    margin: '1rem 0 0 0'
  },
  subRow: {
    display: 'flex',
    flexFlow: 'row wrap',
    padding: '.5rem 0 .5rem 0',
    justifyContent: 'space-between',
    cursor: 'pointer'
  },
  selected: {
    color: '#4080ff'
  },
  nameWrapper: {
    maxWidth: '10rem',
    display: 'flex',
    margin: '0 1rem 0 0',
    ':hover': {
      color: '#757575'
    }
  },
  iconWrapper: {
    color: '#3a3a3a',
    display: 'flex',
    margin: '0 0 0 1rem',
    verticalAlign: 'middle',
    ':hover': {
      color: '#c62828'
    }
  },
  addSubRow: {
    display: 'flex',
    flexFlow: 'column no-wrap',
    margin: '0 0 1rem 0',
    border: '1px solid #e6e6e6'
  },
  addSubInput: {
    display: 'flex',
    flex: 8,
    backgroundColor: 'transparent',
    border: 'none',
    padding: '.3rem'
  },
  addSubBut: {
    display: 'flex',
    flex: 2,
    flexFlow: 'row no-wrap',
    justifyContent: 'space-around',
    backgroundColor: '#4080ff',
    color: '#fff',
    padding: '.3rem',
    verticalAlign: 'middle',
    cursor: 'pointer',
    ":hover": {
      backgroundColor: '#578fff'
    }
  }
}

const Subjects = (props) => {

  const subjects = props.subjects.map((sub, key) => {
    let style = props.selectedSubject.name === sub.name ? Object.assign({}, styles.subRow, styles.selected) : styles.subRow;
    return(
      <div onClick={() => props.onSubjectSelected(sub)} style={style} key={key}>
        <div key={"name" + key} style={styles.nameWrapper}>{sub.name}</div>
        <div key={"minus" + key} style={styles.iconWrapper}>
        <i onClick={() => props.onDeleteSubject(sub)} className="fa fa-minus"></i>
        </div>
      </div>
    );
  });

  return(
    <div style={styles.layout}>
      <div style={styles.addSubRow}>
        <input style={styles.addSubInput} ref={input => {this.newSub = input}}/>
        <div onClick={() => {
          props.onAppendSubject(this.newSub.value);
          this.newSub.value = '';
        }} key={'addSubBut'} style={styles.addSubBut}><span>Pidat</span></div>
      </div>
      {subjects}
    </div>
  );
}

export default Radium(Subjects);


