import React, { Component } from 'react'
import Radium from 'radium'
import DatesAndTimes from './DatesAndTimes'
import Table from './TableContainer'
import id from './keyKeeper'
import Subjects from './Subjects'

let lichy = [
  {
    dId: id(),
    dayNo: 0,
    type: 1,
    lessons: [
      {title: 'uit matematika pro informatiky', type: 2, sId: 0},
      {title: 'anglitina', type: 2, sId: 1},
      {title: 'Objektov modelovn', type: 1, sId: 6},
      {title: 'Zklady ekonomiky', type: 1, sId: 9}
    ]
  },
  {
    dId: id(),
    dayNo: 3,
    type: 1,
    lessons: [
      {title: 'architektura pota', type: 1, sId: 0},
      {title: 'statistika', type: 1, sId: 2},
      {title: 'Operan systmy a potaov st', type: 1, sId: 3},
    ]
  },
  {
    dId: id(),
    dayNo: 4,
    type: 2,
    lessons: [
      {title: 'nmina', type: 2, sId: 0},
      {title: 'znakovac jazyky', type: 1, sId: 6},
      {title: '', type: 3, sId: 6},
      {title: 'operan a systmov analza', type: 1, sId: 3},
      {title: 'informan systmy', type: 1, sId: 2},
    ]
  }
];

let sudy = [
  {
    dId: id(),
    dayNo: 4,
    type: 2,
    lessons: [
      {title: 'nmina', type: 2, sId: 0},
      {title: 'znakovac jazyky', type: 1, sId: 6},
      {title: '', type: 3, sId: 6},
      {title: 'operan a systmov analza', type: 1, sId: 3},
      {title: 'informan systmy', type: 1, sId: 2},
    ]
  }
]

const subjects = [
  {name: 'matika', sId: 0},
  {name: 'lol', sId: 1},
  {name: 'test', sId: 2},
  {name: 'nevim', sId: 3},
  {name: 'lalaa', sId: 4},
]

class App extends Component {
  constructor(props){
    super(props);

    this.state = {
      termines: {
        startTime: {
          hours: 8,
          mins: 45
        },
        lessonDuration: 90,
        pauseDuration: 15,
      },
      showWeekType: 0,
      days: [lichy, sudy],
      subjects: subjects,
      selectedSubject: subjects[0],
      showSubjects: true
    }

    this.onTerminesCange = this.onTerminesCange.bind(this);
    this.onWeekTypeChange = this.onWeekTypeChange.bind(this);
    this.onContentChange = this.onContentChange.bind(this);
    this.onSubjectSelected = this.onSubjectSelected.bind(this);
    this.onAppendSubject = this.onAppendSubject.bind(this);
    this.onDeleteSubject = this.onDeleteSubject.bind(this);
    this.onPanelToggle = this.onPanelToggle.bind(this);
  }

  onPanelToggle(){
    this.setState({showSubjects: !this.state.showSubjects})
  }

  onTerminesCange(newObj){
    this.setState({termines: newObj});
  }

  onWeekTypeChange(type){
    this.setState({showWeekType: type});
  }

  onContentChange(data){
    let copy = [];
    for (let obj of data){
      if(obj.lessons && obj.lessons.length > 0) {
        copy.push(obj);
      }
    }

    let days = Array.from(this.state.days);
    days[this.state.showWeekType] = copy;
    console.log(days);
    this.setState({days});
  }

  onSubjectSelected(sub){
    this.setState({selectedSubject: sub});
  }

  onAppendSubject(name){
    if(name.length > 40) return;
    let sub = {name, id: this.state.subjects.length}
    let subjects = [...this.state.subjects, sub];
    this.setState({subjects});
  }

  onDeleteSubject(sub){
    let subjects = [];
    for (let subject of this.state.subjects){
      if(subject.sId !== sub.sId){
        subjects.push(subject);
      }
    }
    this.setState({subjects});
  }

  render() {
    return (
      <div style={styles.layout}>
        <div style={styles.contentContainer}>
          <Table
            selectedSubject={this.state.selectedSubject}
            onContentChange={this.onContentChange}
            onWeekTypeChange={this.onWeekTypeChange}
            weekType={this.state.showWeekType}
            termines={this.state.termines}
            days={this.state.days[this.state.showWeekType]} />
        </div>

        <div style={styles.panelContainer}>

          <div style={styles.toggleContainer} onClick={this.onPanelToggle}>
            <div key={'togglePredmety'} style={this.state.showSubjects ? styles.toggleEnabled : stylePrototypes.toggle}>
              Pedmty
            </div>
            <div key={'toggleNastaveni'} style={this.state.showSubjects ? stylePrototypes.toggle : styles.toggleEnabled}>
              Nastaven
            </div>
            {this.state.showSubjects ? '' : ''}
          </div>

          <div style={this.state.showSubjects ? {display: 'none'} : styles.panelContentContainer}>
            <DatesAndTimes onChange={this.onTerminesCange} termines={this.state.termines} />
          </div>

          <div style={this.state.showSubjects ? styles.panelContentContainer : {display: 'none'}}>
            <Subjects
              onDeleteSubject={this.onDeleteSubject}
              onAppendSubject={this.onAppendSubject}
              onSubjectSelected={this.onSubjectSelected}
              selectedSubject={this.state.selectedSubject}
              subjects={this.state.subjects} />
          </div>
        </div>
      </div>
    );
  }
}

const stylePrototypes = {
  toggle: {
    display: 'flex',
    flexFlow: 'row no-wrap',
    padding: '.3rem',
    flex: 1,
    cursor: 'pointer',
    ':hover': {
      color: '#757575'
    }
  }
}

const styles = {
  layout: {
    display: 'flex',
    width: '100%',
    height: '100%',
    flexFlow: 'row wrap',
    overflowY: 'auto',
  },
  contentContainer: {
    display: 'flex',
    flexFlow: 'row wrap',
    flex: 8,
    padding: '1rem',
    borderRight: '2px solid #e0e0e0'
  },
  panelContainer: {
    display: 'flex',
    flex: 2,
    flexFlow: 'column wrap',
    justifyContent: 'space-around',
    padding: '1rem'
  },
  panelToggle: {
    display: 'flex'
  },
  panelContentContainer: {
    display: 'flex',
    flex: 1
  },
  toggleEnabled: {
    ...stylePrototypes.toggle,
    backgroundColor: '#fff'
  },
  toggleContainer: {
    display: 'flex',
    flexFlow: 'row no-wrap',
    justifyContent: 'space-between',
    border: '2px solid #e0e0e0',
    backgroundColor: '#e0e0e0'
  }
}

export default Radium(App);



