import React from 'react'
import PropTypes from 'prop-types'

const styles = {
  layout: {
    color: '#3a3a3a',
    display: 'flex',
    flex: 1,
    flexFlow: 'column wrap',
    margin: '1rem 0 0 0'
  },
  row: {
    display: 'flex',
    margin: '.5rem 0 .5rem 0'
  }
}

const DatesAndTimes = (props) =>
{
      const handleChange = ( e ) => {
      props.onChange({
        pauseDuration: parseInt(this.pMins.value, 10),
        lessonDuration: parseInt(this.lMins.value, 10),
        startTime: {
          hours: parseInt(this.flsHour.value, 10),
          mins: parseInt(this.flsMins.value, 10)
        }
      });
    }

    let allowedHours = ['07', '08'];
    allowedHours = allowedHours.map(( hour, key ) => {
      return(
        <option value={parseInt(hour, 10)} key={key}>{hour}</option>
      );
    });

    let allowedMins = ['00', '05', '10', '15', '20', '25', '30', '35', '40', '45', '50', '55'];
    allowedMins = allowedMins.map(( mins, key ) => {
      return(
        <option value={parseInt(mins, 10)} key={key}>{mins}</option>
      );
    });

    let pauseMins = [5, 10, 15, 20, 25, 30];
    pauseMins = pauseMins.map(( pMin, key ) => {
      return(
        <option value={pMin} key={key}>{pMin}</option>
      );
    });

    let lessonMins = [45, 90, 100];
    lessonMins = lessonMins.map(( lMin, key ) => {
      return(
        <option value={lMin} key={key}>{lMin}</option>
      );
    });

    return (
      <div style={styles.layout}>
        <div style={styles.row}>First lesson starts at:&nbsp;
          <select ref={ (select)=>{this.flsHour = select} } defaultValue={ props.termines.startTime.hours } onChange={ handleChange }>
            {allowedHours}
          </select>&nbsp;:&nbsp;
          <select ref={ (select)=>{this.flsMins = select} } defaultValue={ props.termines.startTime.mins } onChange={ handleChange }>
            {allowedMins}
          </select>
        </div>
        <div style={styles.row}>Pause time:&nbsp;
          <select ref={ (select)=>{this.pMins = select} } defaultValue={ props.termines.pauseDuration } onChange={ handleChange }>
            {pauseMins}
          </select>&nbsp;min
        </div>
        <div style={styles.row}>Lesson time:&nbsp;
          <select ref={ (select)=>{this.lMins = select} } defaultValue={ props.termines.lessonDuration } onChange={ handleChange }>
            {lessonMins}
          </select>&nbsp;min
        </div>
      </div>
    );
}

DatesAndTimes.propTypes = {
  termines: PropTypes.object.isRequired
};

export default DatesAndTimes;


