import React from 'react'
import PropTypes from 'prop-types'
import Radium from 'radium'

import { Cell } from './Layout'

const styles = {
  container: {
    display: 'block',
    position: 'relative',
    height: '100%',
    width: '100%',
    overflowY: 'auto',
  },
  controlsContainer: {
    position: 'absolute',
    maxWidth: '200px',
    boxShadow: '0 2px 11px rgba(0,0,0,0.2)',
    backgroundColor: '#fff',
    padding: '0 .5rem 0 .5rem',
    border: '1px solid #e0e0e0',
  },
  control: {
    margin: '.5rem 0 .5rem 0',
    cursor: 'pointer',
    ':hover': {
      color: "#4080ff"
    }
  },
  content: {
    position: 'absolute',
    margin: '.3rem 0 0 0'
  },
  lessonType: {
    textAlign: 'right',
    fontSize: 'smaller'
  }
}

const CellComponent = (props) => {

  let contentStyle = props.showControls ? {display: 'none'} : {display: 'block'};
  let controlsStyle = props.showControls ? {} : {display: 'none'};

  return(
    <Cell onMouseOut={props.onHovered} onMouseOver={props.onHovered}>
      <div style={Object.assign({}, styles.controlsContainer, controlsStyle)}>
        <div key={'pripnout'} style={styles.control} onClick={() => props.onPinSubject(props.serial)}>
          <span>Pipnout {props.selectedSubject.name}</span>
        </div>
        <div style={styles.control} onClick={() => props.onClearSubject(props.serial)} key={'odepnout'}>
          <span>Odepnout</span>
        </div>
        <div style={styles.control} onClick={() => props.onToggleLessonType(props.serial)} key={'toggleType'}>
          <span>Pepnout typ</span>
        </div>
      </div>
      <div style={Object.assign({}, styles.container, contentStyle)}>
        <div style={styles.lessonType}>{props.lesson.type}</div>
        <div style={styles.content}>{props.lesson.title}</div>
      </div>
    </Cell>
  );
}

CellComponent.propTypes = {

}

export default Radium(CellComponent);


