import React from 'react'
import PropTypes from 'prop-types'
import Radium from 'radium'

import RowContainer from './RowContainer'
import {Row, Cell} from './Layout'
import id from './keyKeeper'

const stylePrototypes = {
  weekBut: {
    display: 'flex',
    flexFlow: 'row no-wrap',
    whiteSpace: 'nowrap',
    border: 'none',
    backgroundColor: '#fff',
    padding: '.3rem',
    color: '#3a3a3a',
    cursor: 'pointer'
  }
}

const styles = {
  layout: {
    display: 'table',
    borderCollapse: 'collapse',
    tableLayout: 'fixed',
    width: '100%',
    height: '100%'
  },
  tableBar: {
    display: 'flex',
    flexFlow: 'row no-wrap',
  },
  buttonEnabled: {
    ...stylePrototypes.weekBut
  },
  buttonDisabled: {
    ...stylePrototypes.weekBut,
    backgroundColor: '#e6e6e6'
  },
  weekTypeControlls: {
    margin: 'auto 1rem auto 0',
    display: 'flex',
    flexFlow: 'row no-wrap',
    border: '2px solid #eaeaea',
  },
  lockBut: {
    cursor: 'pointer',
    display: 'flex',
    flexFlow: 'row no-wrap',
    margin: '1rem',
    justifyContent: 'space-around',
    ":hover": {
      color: '#4080ff'
    }
  },
  tableLockText: {
    margin: 'auto .5rem'
  },
  timeContainer: {
    fontSize: '13px',
    display: 'flex',
    width: '100%',
    height: '100%',
    flexFlow: 'row no-wrap',
    justifyContent: 'space-between',
  },
  timeStart: {
    display: 'flex',
    margin: '0 0 0 .5rem',
  },
  timeEnd: {
    display: 'flex',
    margin: '0 .5rem 0 0'
  }
}

const Table = (props) => {
  let k = 0;

  const rows = props.days.map((day, key) => {
    return <RowContainer
      selectedSubject={props.selectedSubject}
      tableLocked={props.tableLocked}
      onContentChange={props.onContentChange}
      key={id()}
      day={day} />
  });

  const timeRow = props.times.map((time) => {
    return !time.start.hour ? null : (
      <Cell key={id()} style={{border: 'none', height: '1rem'}}>
        <div style={styles.timeContainer}>
          <div style={styles.timeStart}>{time.start.hour}:{time.start.min}</div>
          <div style={styles.timeEnd}>{time.end.hour}:{time.end.min}</div>
        </div>
      </Cell>
    )
  });
  timeRow.unshift(<Cell style={{border: 'none'}} key={id()} />);

  return (
    <div style={styles.layout}>
      <div style={styles.tableBar}>
        <div style={styles.weekTypeControlls}>
          <div style={props.weekType == 0 ? styles.buttonEnabled : styles.buttonDisabled}
               key={id()} onClick={() => props.onWeekTypeChange(0)}>
            Lich tden
          </div>
          <div style={props.weekType == 1 ? styles.buttonEnabled : styles.buttonDisabled}
               key={id()}
               onClick={() => props.onWeekTypeChange(1)}>
            Sud tden
          </div>
        </div>
        <div key={'lockBut'} onClick={props.onToggleTableLock} style={styles.lockBut}>
          <i style={{margin: 'auto'}} className={props.tableLocked ? 'fa fa-lock' : 'fa fa-unlock'}></i>
          <div style={styles.tableLockText}>{props.tableLocked ? 'Odemknout' : 'Uzamknout'}</div>
        </div>
      </div>
      <Row>
        {timeRow}
      </Row>
      {rows}
    </div>
  );
}
Table.propTypes = {

}

export default Radium(Table);



