import React from 'react'
import PropTypes from 'prop-types'
import Radium from 'radium'

import CellContainer from './CellContainer'
import {Row, Cell} from './Layout'
import id from './keyKeeper'

const styles = {
  container: {
    display: 'flex',
    flex: 1,
    flexFlow: 'column wrap',
    justifyContent: 'space-around',
    padding: '0 1rem 0 1rem',
    cursor: 'pointer',
    ':hover': {
      color: '#4080ff'
    }
  },
  wrapper: {
    display: 'flex',
    flexFlow: 'column wrap',
    height: '100%',
    margin: 0,
    padding: 0
  },
  icon: {
  },
  dayName: {
    display: 'flex',
    flexFlow: 'column wrap',
    justifyContent: 'space-around',
    width: '100%',
    height: '100%',
  }
}

const fixedIds = [id(), id()];

const RowComponent = (props) => {

  const cells = !props.day.lessons ? [] : props.day.lessons.map((lesson, key) => {
    return <CellContainer
      onToggleLessonType={props.onToggleLessonType}
      tableLocked={props.tableLocked}
      selectedSubject={props.selectedSubject}
      serial={key}
      onPinSubject={props.onPinSubject}
      onClearSubject={props.onClearSubject}
      key={id()}
      lesson={lesson} />
  });

  if(!props.tableLocked) {
    cells.push(
      <Cell style={{border: 'none'}} fixedHeight="true" key={id()}>
        <div style={styles.wrapper}>
          <div key={fixedIds[1]} style={styles.container} onClick={props.onAppendLesson}>
            <i style={styles.icon} className="fa fa-plus"></i>
          </div>
          <div key={fixedIds[2]} style={styles.container} onClick={props.onPopLesson}>
            <i style={styles.icon} className="fa fa-minus"></i>
          </div>
        </div>
      </Cell>
    );
  }

  return (
    <Row>
      <Cell><div style={styles.dayName}>
        <span style={{margin: 'auto'}}>{props.day.dayName}</span>
        </div>
      </Cell>
      {cells}
    </Row>
  )
}

RowComponent.propTypes = {

}

export default Radium(RowComponent);



